.signIn {
  display: flex;
  min-height: 100vh;
  animation: transitionIn 0.75s;
  width: 100%;
  flex: 1;
}

.signUpForm {
  position: absolute;
  left: 45%;
  top: 45%;
  display: flex;
  flex-direction: column;
  background-color: black;
  color: white;
  justify-content: center;
  align-items: left;
  gap: 35px;
  border: 2px solid white;
  border-radius: 1em;
  padding: 40px;
  transform: translate(-50%, -50%);
  z-index: 2;
}

.input {
  width: 85%;
  height: 35px;
  font-size: 20px;
  color: rgb(29, 161, 242);
  background-color: black;
  border: 1px solid gray;
  align-self: center;
}

.input:focus {
  border: 2px solid rgb(29, 161, 242);
  outline: none;
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgb(29, 161, 242);
  opacity: 1; /* Firefox */
}

.home--signIn {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 50px;
  color: white;
  background-color: black;
  gap: 1%;
  width: 100%;
  position: relative;
}

button.input {
  color: white;
  background-color: rgb(29, 161, 242);
}

.homeImageDiv {
  display: flex;
  width: 50%;
  position: relative;
  justify-content: center;
  align-items: center;
}

.homeImage {
  object-fit: contain;
  height: 100%;
}

.signIn--button {
  width: 55%;
  border-radius: 2em;
  padding: 12px;
  border: black;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 15px;
  font-weight: bold;
}

.header2 {
  width: 55%;
  text-align: center;
  border-bottom: 1px solid white;
  line-height: 0.1em;
  margin: 10px 0 20px;
}

h2 span {
  background: black;
  padding: 0 10px;
}

.signIn--button:hover {
  cursor: pointer;
  opacity: 0.8;
}

@media screen and (max-width: 950px) {
  .homeImageDiv {
    display: none;
  }

  .home--signIn {
    align-items: center;
    text-align: center;
    display: flex;
    width: 100%;
    flex: 1;
    gap: 10px;
    padding: 60px 10px 0 0;
  }
  .signIn--button {
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 650px) {
  .signUpForm {
    padding: none;
    height: 100%;
    width: 100%;
    margin-left: 4%;
    overflow-y: hidden;
    overflow-x: hidden;
  }
  .homeImageDiv {
    display: none;
  }
  .happening {
    display: none;
  }
  .signIn {
    overflow-y: hidden;
    overflow-x: hidden;
  }
}
